module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport cx from 'classnames'\nimport { toaster } from '../../toaster'\nimport { withTheme } from '../../theme'\nimport { Pane } from '../../layers'\nimport { TableRowConsumer } from './TableRowContext'\nimport manageTableCellFocusInteraction from './manageTableCellFocusInteraction'\n\nclass TableCell extends PureComponent {\n  static propTypes = {\n    /**\n     * Composes the Pane component as the base.\n     */\n    ...Pane.propTypes,\n\n    /*\n    * Makes the TableCell focusable. Used by EditableCell.\n    * Will add tabIndex={-1 || this.props.tabIndex}.\n    */\n    isSelectable: PropTypes.bool,\n\n    /**\n     * The appearance of the table row. Default theme only support default.\n     */\n    appearance: PropTypes.string.isRequired,\n\n    /**\n     * Optional node to be placed on the right side of the table cell.\n     * Useful for icons and icon buttons.\n     */\n    rightView: PropTypes.node,\n\n    /**\n     * Theme provided by ThemeProvider.\n     */\n    theme: PropTypes.object.isRequired,\n\n    /**\n     * Class name passed to the table cell.\n     * Only use if you know what you are doing.\n     */\n    className: PropTypes.string\n  }\n\n  static defaultProps = {\n    appearance: 'default',\n    onSelect: () => {},\n    onDeselect: () => {}\n  }\n\n  static styles = {\n    paddingX: 12,\n    boxSizing: 'border-box',\n    flex: 1,\n    display: 'flex',\n    alignItems: 'center',\n    flexShrink: 0,\n    overflow: 'hidden'\n  }\n\n  handleKeyDown = e => {\n    if (this.props.isSelectable) {\n      const { key } = e\n      if (\n        key === 'ArrowUp' ||\n        key === 'ArrowDown' ||\n        key === 'ArrowLeft' ||\n        key === 'ArrowRight'\n      ) {\n        try {\n          manageTableCellFocusInteraction(key, this.mainRef)\n        } catch (err) {\n          toaster.danger('Keyboard interaction not possible')\n          console.error('Keyboard control not impossible', err)\n        }\n      } else if (key === 'Escape') {\n        this.mainRef.blur()\n      }\n    }\n\n    if (typeof this.props.onKeyDown === 'function') {\n      this.props.onKeyDown(e)\n    }\n  }\n\n  onRef = ref => {\n    this.mainRef = ref\n\n    if (typeof this.props.innerRef === 'function') {\n      this.props.innerRef(ref)\n    }\n  }\n\n  handleClick = e => {\n    if (typeof this.props.onClick === 'function') {\n      this.props.onClick(e)\n    }\n  }\n\n  render() {\n    const {\n      innerRef,\n      theme,\n      children,\n      appearance,\n      onSelect,\n      onDeselect,\n      onClick,\n      onKeyPress,\n      onKeyDown,\n      isSelectable,\n      tabIndex = -1,\n      className,\n      rightView,\n      ...props\n    } = this.props\n\n    const themedClassName = theme.getTableCellClassName(appearance)\n\n    return (\n      <TableRowConsumer>\n        {height => {\n          return (\n            <Pane\n              innerRef={this.onRef}\n              height={height}\n              className={cx(themedClassName, className)}\n              tabIndex={isSelectable ? tabIndex : undefined}\n              data-isselectable={isSelectable}\n              onClick={this.handleClick}\n              onKeyDown={this.handleKeyDown}\n              {...TableCell.styles}\n              {...props}\n            >\n              {children}\n              {rightView ? rightView : null}\n            </Pane>\n          )\n        }}\n      </TableRowConsumer>\n    )\n  }\n}\n\nexport default withTheme(TableCell)\n"