module.exports = "import React from 'react'\nimport PropTypes from 'prop-types'\nimport { css } from 'ui-box'\nimport { Pane } from '../../layers'\nimport { Paragraph, Heading } from '../../typography'\nimport { Overlay } from '../../overlay'\nimport { Button, IconButton } from '../../buttons'\nimport { withTheme } from '../../theme'\n\nconst animationEasing = {\n  deceleration: `cubic-bezier(0.0, 0.0, 0.2, 1)`,\n  acceleration: `cubic-bezier(0.4, 0.0, 1, 1)`\n}\n\nconst ANIMATION_DURATION = 200\n\nconst openAnimation = css.keyframes('openAnimation', {\n  from: {\n    transform: 'scale(0.8)',\n    opacity: 0\n  },\n  to: {\n    transform: 'scale(1)',\n    opacity: 1\n  }\n})\n\nconst closeAnimation = css.keyframes('closeAnimation', {\n  from: {\n    transform: 'scale(1)',\n    opacity: 1\n  },\n  to: {\n    transform: 'scale(0.8)',\n    opacity: 0\n  }\n})\n\nconst animationStyles = {\n  '&[data-state=\"entering\"], &[data-state=\"entered\"]': {\n    animation: `${openAnimation} ${ANIMATION_DURATION}ms ${\n      animationEasing.deceleration\n    } both`\n  },\n  '&[data-state=\"exiting\"]': {\n    animation: `${closeAnimation} ${ANIMATION_DURATION}ms ${\n      animationEasing.acceleration\n    } both`\n  }\n}\n\nclass Dialog extends React.Component {\n  static propTypes = {\n    /**\n     * Children can be a string, node or a function accepting `({ close })`.\n     * When passing a string, <Paragraph /> is used to wrap the string.\n     */\n    children: PropTypes.oneOfType([PropTypes.node, PropTypes.func]).isRequired,\n\n    /**\n     * The intent of the Dialog. Used for the button.\n     */\n    intent: PropTypes.oneOf(['none', 'success', 'warning', 'danger'])\n      .isRequired,\n\n    /**\n     * When true, the dialog is shown.\n     */\n    isShown: PropTypes.bool,\n\n    /**\n     * Title of the Dialog. Titles should use Title Case.\n     */\n    title: PropTypes.node,\n\n    /**\n     * When true, the header with the title and close icon button is shown.\n     */\n    hasHeader: PropTypes.bool,\n\n    /**\n     * When true, the footer with the cancel and confirm button is shown.\n     */\n    hasFooter: PropTypes.bool,\n\n    /**\n     * When true, the cancel button is shown.\n     */\n    hasCancel: PropTypes.bool,\n\n    /**\n     * Function that will be called when the exit transition is complete.\n     */\n    onCloseComplete: PropTypes.func,\n\n    /**\n     * Function that will be called when the enter transition is complete.\n     */\n    onOpenComplete: PropTypes.func,\n\n    /**\n     * Function that will be called when the confirm button is clicked.\n     * This does not close the Dialog. A close function will be passed\n     * as a paramater you can use to close the dialog.\n     *\n     * `onConfirm={(close) => close()}`\n     */\n    onConfirm: PropTypes.func,\n\n    /**\n     * Label of the confirm button.\n     */\n    confirmLabel: PropTypes.string,\n\n    /**\n     * When true, the confirm button is set to loading.\n     */\n    isConfirmLoading: PropTypes.bool,\n\n    /**\n     * When true, the confirm button is set to disabled.\n     */\n    isConfirmDisabled: PropTypes.bool,\n\n    /**\n     * Function that will be called when the cancel button is clicked.\n     * This closes the Dialog by default.\n     *\n     * `onCancel={(close) => close()}`\n     */\n    onCancel: PropTypes.func,\n\n    /**\n     * Label of the cancel button.\n     */\n    cancelLabel: PropTypes.string,\n\n    /**\n     * Width of the Dialog.\n     */\n    width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),\n\n    /**\n     * The space above the dialog.\n     * This offset is also used at the bottom when there is not enough vertical\n     * space available on screen  and the dialog scrolls internally.\n     */\n    topOffset: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),\n\n    /**\n     * The space on the left/right sides of the dialog when there isn't enough\n     * horizontal space available on screen.\n     */\n    sideOffset: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),\n\n    /**\n     * The min height of the body content.\n     * Makes it less weird when only showing little content.\n     */\n    minHeightContent: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),\n\n    /**\n     * Props that are passed to the dialog container.\n     */\n    containerProps: PropTypes.object\n  }\n\n  static defaultProps = {\n    isShown: false,\n    hasHeader: true,\n    hasFooter: true,\n    hasCancel: true,\n    intent: 'none',\n    width: 560,\n    topOffset: '12vmin',\n    sideOffset: '16px',\n    minHeightContent: 80,\n    confirmLabel: 'Confirm',\n    isConfirmLoading: false,\n    isConfirmDisabled: false,\n    cancelLabel: 'Cancel',\n    onCancel: close => close(),\n    onConfirm: close => close()\n  }\n\n  renderChildren = close => {\n    const { children } = this.props\n\n    if (typeof children === 'function') {\n      return children({ close })\n    } else if (typeof children === 'string') {\n      return <Paragraph>{children}</Paragraph>\n    }\n    return children\n  }\n\n  render() {\n    const {\n      title,\n      width,\n      intent,\n      isShown,\n      topOffset,\n      sideOffset,\n      hasHeader,\n      hasFooter,\n      hasCancel,\n      onCloseComplete,\n      onOpenComplete,\n      onCancel,\n      onConfirm,\n      confirmLabel,\n      isConfirmLoading,\n      isConfirmDisabled,\n      cancelLabel,\n      containerProps,\n      minHeightContent\n    } = this.props\n\n    const topOffsetWithUnit = Number.isInteger(topOffset)\n      ? `${topOffset}px`\n      : topOffset\n    const maxHeight = `calc(100% - ${topOffsetWithUnit} * 2)`\n\n    return (\n      <Overlay\n        isShown={isShown}\n        onExited={onCloseComplete}\n        onEntered={onOpenComplete}\n        containerProps={{\n          display: 'flex',\n          alignItems: 'flex-start',\n          justifyContent: 'center'\n        }}\n      >\n        {({ state, close }) => (\n          <Pane\n            role=\"dialog\"\n            backgroundColor=\"white\"\n            elevation={4}\n            borderRadius={8}\n            width={width}\n            maxHeight={maxHeight}\n            marginX={sideOffset}\n            marginY={topOffsetWithUnit}\n            display=\"flex\"\n            flexDirection=\"column\"\n            css={animationStyles}\n            data-state={state}\n            {...containerProps}\n          >\n            {hasHeader && (\n              <Pane\n                padding={16}\n                flexShrink={0}\n                borderBottom=\"muted\"\n                display=\"flex\"\n                alignItems=\"center\"\n              >\n                <Heading is=\"h4\" size={600} flex=\"1\">\n                  {title}\n                </Heading>\n                <IconButton appearance=\"minimal\" icon=\"cross\" onClick={close} />\n              </Pane>\n            )}\n\n            <Pane\n              data-state={state}\n              display=\"flex\"\n              overflowY=\"auto\"\n              padding={16}\n              flexDirection=\"column\"\n              minHeight={minHeightContent}\n            >\n              <Pane>{this.renderChildren(close)}</Pane>\n            </Pane>\n\n            {hasFooter && (\n              <Pane borderTop=\"muted\" clearfix>\n                <Pane padding={16} float=\"right\">\n                  {/* Cancel should be first to make sure focus gets on it first. */}\n                  {hasCancel && (\n                    <Button tabIndex={0} onClick={() => onCancel(close)}>\n                      {cancelLabel}\n                    </Button>\n                  )}\n\n                  <Button\n                    tabIndex={0}\n                    marginLeft={8}\n                    appearance=\"primary\"\n                    isLoading={isConfirmLoading}\n                    disabled={isConfirmDisabled}\n                    onClick={() => onConfirm(close)}\n                    intent={intent}\n                  >\n                    {confirmLabel}\n                  </Button>\n                </Pane>\n              </Pane>\n            )}\n          </Pane>\n        )}\n      </Overlay>\n    )\n  }\n}\n\nexport default withTheme(Dialog)\n"