module.exports = "import React from 'react'\nimport PropTypes from 'prop-types'\nimport { css } from 'ui-box'\nimport { Pane } from '../../layers'\nimport { Overlay } from '../../overlay'\nimport { Position } from '../../constants'\nimport SheetClose from './SheetClose'\n\nconst paneProps = {\n  [Position.LEFT]: {\n    height: '100vh',\n    maxWidth: '100vw',\n    position: 'absolute',\n    left: 0,\n    right: 'auto'\n  },\n  [Position.RIGHT]: {\n    height: '100vh',\n    maxWidth: '100vw',\n    position: 'absolute',\n    right: 0,\n    left: 'auto'\n  },\n  [Position.TOP]: {\n    width: '100vw',\n    position: 'absolute',\n    maxHeight: '100vh',\n    top: 0,\n    bottom: 'auto'\n  },\n  [Position.BOTTOM]: {\n    width: '100vw',\n    maxHeight: '100vh',\n    position: 'absolute',\n    bottom: 0,\n    top: 'auto'\n  }\n}\n\nconst subpaneProps = {\n  [Position.LEFT]: {\n    height: '100vh'\n  },\n  [Position.RIGHT]: {\n    height: '100vh'\n  },\n  [Position.TOP]: {\n    width: '100vw'\n  },\n  [Position.BOTTOM]: {\n    width: '100vw'\n  }\n}\n\nconst animationEasing = {\n  deceleration: `cubic-bezier(0.0, 0.0, 0.2, 1)`,\n  acceleration: `cubic-bezier(0.4, 0.0, 1, 1)`\n}\n\nconst ANIMATION_DURATION = 240\n\nconst withAnimations = (animateIn, animateOut) => {\n  return {\n    '&[data-state=\"entering\"], &[data-state=\"entered\"]': {\n      animation: `${animateIn} ${ANIMATION_DURATION}ms ${\n        animationEasing.deceleration\n      } both`\n    },\n    '&[data-state=\"exiting\"]': {\n      animation: `${animateOut} ${ANIMATION_DURATION}ms ${\n        animationEasing.acceleration\n      } both`\n    }\n  }\n}\n\nconst animationStyles = {\n  [Position.LEFT]: {\n    transform: `translateX(-100%)`,\n    ...withAnimations(\n      css.keyframes('anchoredLeftSlideInAnimation', {\n        from: { transform: `translateX(-100%)` },\n        to: { transform: `translateX(0)` }\n      }),\n      css.keyframes('anchoredLeftSlideOutAnimation', {\n        from: { transform: `translateX(0)` },\n        to: { transform: `translateX(-100%)` }\n      })\n    )\n  },\n  [Position.RIGHT]: {\n    transform: `translateX(100%)`,\n    ...withAnimations(\n      css.keyframes('anchoredRightSlideInAnimation', {\n        from: { transform: `translateX(100%)` },\n        to: { transform: `translateX(0)` }\n      }),\n      css.keyframes('anchoredRightSlideOutAnimation', {\n        from: { transform: `translateX(0)` },\n        to: { transform: `translateX(100%)` }\n      })\n    )\n  },\n  [Position.TOP]: {\n    transform: `translateY(-100%)`,\n    ...withAnimations(\n      css.keyframes('anchoredTopSlideInAnimation', {\n        from: { transform: `translateY(-100%)` },\n        to: { transform: `translateY(0)` }\n      }),\n      css.keyframes('anchoredTopSlideOutAnimation', {\n        from: { transform: `translateY(0)` },\n        to: { transform: `translateY(-100%)` }\n      })\n    )\n  },\n  [Position.BOTTOM]: {\n    transform: `translateY(100%)`,\n    ...withAnimations(\n      css.keyframes('anchoredBottomSlideInAnimation', {\n        from: { transform: `translateY(100%)` },\n        to: { transform: `translateY(0)` }\n      }),\n      css.keyframes('anchoredBottomSlideOutAnimation', {\n        from: { transform: `translateY(0)` },\n        to: { transform: `translateY(100%)` }\n      })\n    )\n  }\n}\n\nclass SideSheet extends React.Component {\n  static propTypes = {\n    /**\n     * Children can be a string, node or a function accepting `({ close })`.\n     */\n    children: PropTypes.oneOfType([PropTypes.node, PropTypes.func]).isRequired,\n\n    /**\n     * When true, the Side Sheet is shown.\n     */\n    isShown: PropTypes.bool,\n\n    /**\n     * Function that will be called when the exit transition is complete.\n     */\n    onCloseComplete: PropTypes.func,\n\n    /**\n     * Function that will be called when the enter transition is complete.\n     */\n    onOpenComplete: PropTypes.func,\n\n    /**\n     * Width of the SideSheet.\n     */\n    width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,\n\n    /**\n     * Properties to pass through the SideSheet container Pane.\n     */\n    containerProps: PropTypes.object,\n\n    /**\n     * Positions the sheet to the top, left, right, or bottom of the screen.\n     */\n    position: PropTypes.oneOf([\n      Position.TOP,\n      Position.BOTTOM,\n      Position.LEFT,\n      Position.RIGHT\n    ]).isRequired\n  }\n\n  static defaultProps = {\n    width: 620,\n    onCloseComplete: () => {},\n    onOpenComplete: () => {},\n    position: Position.RIGHT\n  }\n\n  render() {\n    const {\n      width,\n      isShown,\n      children,\n      containerProps,\n      onOpenComplete,\n      onCloseComplete,\n      position\n    } = this.props\n\n    return (\n      <Overlay\n        isShown={isShown}\n        onExited={onCloseComplete}\n        onEntered={onOpenComplete}\n      >\n        {({ state, close }) => (\n          <Pane\n            width={width}\n            {...paneProps[position]}\n            css={animationStyles[position]}\n            data-state={state}\n          >\n            <SheetClose\n              position={position}\n              data-state={state}\n              isClosing={false}\n              onClick={close}\n            />\n            <Pane\n              elevation={4}\n              backgroundColor=\"white\"\n              overflowY=\"auto\"\n              maxHeight=\"100vh\"\n              data-state={state}\n              width={width}\n              {...subpaneProps[position]}\n              {...containerProps}\n            >\n              {typeof children === 'function' ? children({ close }) : children}\n            </Pane>\n          </Pane>\n        )}\n      </Overlay>\n    )\n  }\n}\n\nexport default SideSheet\n"