module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport { Strong } from '../../typography'\nimport { withTheme } from '../../theme'\n\nclass Badge extends PureComponent {\n  static propTypes = {\n    ...Strong.propTypes,\n\n    /**\n     * The color used for the badge.\n     * When the value is `automatic`, use the hash function to determine the color.\n     */\n    color: PropTypes.string.isRequired,\n\n    /**\n     * Theme provided by ThemeProvider.\n     */\n    theme: PropTypes.object.isRequired\n  }\n\n  static defaultProps = { color: 'neutral', isSolid: false }\n\n  static styles = {\n    display: 'inline-block',\n    boxSizing: 'border-box',\n    height: 16,\n    paddingTop: 0,\n    paddingRight: 6,\n    paddingBottom: 0,\n    paddingLeft: 6,\n    borderRadius: 2,\n    textAlign: 'center',\n    textDecoration: 'none',\n    textTransform: 'uppercase'\n  }\n\n  render() {\n    const { theme, color: propsColor, isSolid, ...props } = this.props\n\n    const { color, backgroundColor } = theme.getBadgeProps({\n      isSolid,\n      color: propsColor\n    })\n\n    return (\n      <Strong\n        size={300}\n        {...Badge.styles}\n        color={color}\n        backgroundColor={backgroundColor}\n        {...props}\n      />\n    )\n  }\n}\n\nexport default withTheme(Badge)\n"